body {
  background-color: rgba(132, 103, 103, .58);
  font-family: Roboto, Verdana, Tahoma, sans-serif;
}

img {
  max-width: 600px;
  width: 100%;
  height: 100%;
  border: none;
}

.cat-info {
  gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
}

p {
  font-style: italic;
}

p > span {
  font-weight: 600;
}

.breed-select {
  max-width: 300px;
  margin: 30px;
}

.loader, .error {
  margin: 0 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  margin: 20px auto auto 150px;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 24px;
  background: #fff;
  position: absolute;
  bottom: -20px;
  left: 46px;
  transform: rotate(-45deg);
}

@keyframes animloader {
  0% {
    transform: translate(-10px, -10px);
  }

  25% {
    transform: translate(-10px, 10px);
  }

  50% {
    transform: translate(10px, 10px);
  }

  75% {
    transform: translate(10px, -10px);
  }

  100% {
    transform: translate(-10px, -10px);
  }
}

.is-hidden {
  display: none;
}

